*{
  margin: 0px;
  padding: 0px;
}
.App{
  background-color: black;
  color: rgb(236, 221, 192);

}

.App{
  height: 100vh;
  justify-content: center;
  text-align: center;
  display: flex;
  flex-direction: column;
  /* font-size: large; */
}

.App h1,h3{
  margin: 10px;
}
.App h1{
  letter-spacing: 1.5px;
  /* font-family: 'Playfair Display', serif; */
  font-family: 'Russo One', sans-serif;
  font-size: 42px;
  font-weight: 200;

}
.App h3{
  letter-spacing: 1.5px;
  font-family: 'Source Code Pro', monospace;
  font-size: 22px;
  /* font-weight: 100; */
}
.homepage-subtitle{
  display: flex;
  justify-content: center;
  align-items: center;
}


.homepage-subtitle span{

  width: 3px;
  
  animation-name: spanAnimation1; 
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  border-radius: 5px;
}
@media (min-width: 1211px) {

  .homepage-subtitle span{

  }
  @keyframes spanAnimation1 {
    0% {
      height: 5px;

    background-color: rgb(236, 221, 192);


    }
    100%{

      height: 22px;
      background-color: white;

    }
  }
}

  @media (max-width: 1211px) {

    @keyframes spanAnimation1 {
      0% {
        height: 5%;

        background-color: white;
    
      }
      100%{
        height: 80%;

    
      background-color: rgb(236, 221, 192);
        
      }
    }
    
  }
  @media (max-width: 640px) {
    .homepage-subtitle{
      flex-direction: column;
    }
    .homepage-subtitle span{
      height: 2px;

      animation-name: spanAnimation2; 
      animation-duration: 2.5s;

      border-radius: 5px;
    }
    
    @keyframes spanAnimation2 {
      0% {
        width: 6%;
    
      background-color: white;

    
      }
      100%{
        width: 90%;

        background-color: rgb(236, 221, 192);
    
      }
    }
    
  }
