  .projects-cards {
    margin: 25px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  .card {
    text-align: center;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 27%;

    box-shadow: 0 4px 8px 0 rgba(255, 255, 255, 0.479);
    transition: 0.3s;
    min-height: 50vh;
    margin: 15px;

  }
  .card p {
    margin: 10px;
  }
  .card:hover {
    box-shadow: 0 8px 16px 0 rgb(255, 255, 255);
  }
  .card img {
    padding: 9px;
    max-height: 50vh;
    transition: transform .7s ease-out,border-radius .8s ease-out;
    /* transition:  .5s ease-out; */
    max-width: 100%;
    overflow: hidden;

    object-fit: scale-down;
  }
  .card img:hover{
    transform: scale(1.19);
    border-radius: 15%;
  }
  .card h3 {
    text-align: center;
  }
  .card .container p {
    padding: 5px;
    text-align: justify;
    
  }
   .card-sources{
       padding: 10px;
    text-align: left;
    display: flex;
  }
  .card-sources a{
    margin-left: 15px;

  }
  .card-sources a:hover{
    color: rgb(236, 221, 192);

  }
  .card-sources-link{
    transition: transform 0.2s ease-out;

  }
  .card-sources-link:hover{
    transform: translateY(-4px);

  }
  @media  (max-width: 850px) {
    .card {
      width: 45%;
      box-shadow: 0 4px 8px 0 rgba(255, 255, 255, 0.479);

    }
  }
  
  @media  (max-width: 600px) {
    .card {
      width: 85%;
      box-shadow: 0 4px 8px 0 rgba(255, 255, 255, 0.479);

    }
  }
